import React from 'react';
import './Menu.css';
import { Link, useNavigate } from 'react-router-dom';
import { menuProps } from './MenuProps';

export function Menu({ toggleOpenMenu, openLoginPopup, type }: menuProps) {
  const navigate = useNavigate();

  const handleAvatarClick = () => {
    const token = localStorage.getItem('authToken');
    if (token) {
      navigate('/personal'); //    
    } else {
      if (openLoginPopup) {
        openLoginPopup(); //    
      }
    }
  };

  if (type === 'footer') {
    return (
      <nav className={`menu menu_type_footer`}>
        <Link to='/' className='menu__link menu__link_type_footer'></Link>
        <Link to='/about-us' className='menu__link menu__link_type_footer'> </Link>
        <Link to='/events' className='menu__link menu__link_type_footer'></Link>
        <Link to='/directions' className='menu__link menu__link_type_footer'></Link>
        <Link to='/blog' className='menu__link menu__link_type_footer'></Link>
        <Link to='/contacts' className='menu__link menu__link_type_footer'></Link>
      </nav>
    );
  } else {
    return (
      <>
        <nav className={`menu`}>
          <Link to='/' className='menu__link' onClick={toggleOpenMenu}></Link>
          <Link to='/about-us' className='menu__link' onClick={toggleOpenMenu}> </Link>
          <Link to='/events' className='menu__link' onClick={toggleOpenMenu}></Link>
          <Link to='/directions' className='menu__link' onClick={toggleOpenMenu}></Link>
          <Link to='/blog' className='menu__link' onClick={toggleOpenMenu}></Link>
          <Link to='/contacts' className='menu__link' onClick={toggleOpenMenu}></Link>
          <div className='menu__link menu__link_acc-logo' onClick={handleAvatarClick}></div>
        </nav>
      </>
    );
  }
}
