import React, { useEffect, useState } from 'react';
import './CurrentLocation.css';
import { useLocation } from 'react-router-dom';

export function CurrentLocation() {

    const location = useLocation();
    
    const [ locationName, setLocationName ] = useState('');

    useEffect(() => {
        location.pathname === ('/') && setLocationName('');
        location.pathname.includes('/about-us') && setLocationName(' ');
        location.pathname.includes('/events') && setLocationName('');
        location.pathname.includes('/directions') && setLocationName('');
        location.pathname.includes('/blog') && setLocationName('');
        location.pathname.includes('/contacts') && setLocationName('');
        location.pathname.includes('/SkateboardEventPage') && setLocationName('  ');
        location.pathname.includes('/personal') && setLocationName('');

    }, [location])


  if(location.pathname === '/') {
    return (
      <>
      </>
    )
  } else {
    return (
    <div className='current-location'>
      <p className='current-location__text'>{` / ${locationName}`}</p>
    </div>)
  }
}
