import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './Personalacc.css';
import profilePhoto from '../../images/profile.svg';
import CardEvent from '../../images/cardevent.png';
import { getToken } from '../../utils/token';

export const Personal: React.FC = () => {
    const navigate = useNavigate();

    useEffect(() => {
        const token = getToken();
        if (!token) {
            navigate('/login');
        } else {
            console.log(' :', token);
        }
    }, [navigate]);

    return (
        <div className='personal'>
            <div className='personal__sidebar'>
                <img src={profilePhoto} alt="profile" className="personal__profile-photo" />
                <p className="personal__sidebar-text">  </p>
                <a href="#my-events" className="personal__sidebar-link"> </a>
                <a href="#event-history" className="personal__sidebar-link"> </a>
                <a href="#settings" className="personal__sidebar-link"></a>
                <a href="#support" className="personal__sidebar-link"> </a>
                <a href="#support" className="personal__sidebar-link"></a>
            </div>
            <div className='personal__content'>
                <div className='personal__header'>
                    <h1> </h1>
                    <select className='personal__header-select'>
                        <option> </option>
                        <option> </option>
                    </select>
                </div>
                <div className='personal__cards'>
                    <div className='personal__card'>
                        <img src={CardEvent} alt="event" />
                        <p className='personal__card-text'>   </p>
                        <p className='personal__card-textsmall'>15 , . </p>
                    </div>
                    <div className='personal__card'>
                        <img src={CardEvent} alt="event" />
                        <p className='personal__card-text'>   </p>
                        <p className='personal__card-textsmall'>15 , . </p>
                    </div>
                    <div className='personal__card'>
                        <img src={CardEvent} alt="event" />
                        <p className='personal__card-text'>   </p>
                        <p className='personal__card-textsmall'>15 , . </p>
                    </div>
                </div>
                <div className='personal__buttons'>
                    <button className='personal__button-event'> </button>
                    <button className='personal__button-create' onClick={() => navigate('/create-event-profile')}></button>
                </div>
                <div className='personal__viewed-section'>
                    <h2 className='personal__viewed-title'> :</h2>
                    <div className='personal__viewed-cards'>
                        <div className='personal__card'>
                            <img src={CardEvent} alt="event" />
                            <p className='personal__card-text'>   </p>
                            <p className='personal__card-textsmall'>15 , . </p>
                        </div>
                        <div className='personal__card'>
                            <img src={CardEvent} alt="event" />
                            <p className='personal__card-text'>   </p>
                            <p className='personal__card-textsmall'>15 , . </p>
                        </div>
                        <div className='personal__card'>
                            <img src={CardEvent} alt="event" />
                            <p className='personal__card-text'>   </p>
                            <p className='personal__card-textsmall'>15 , . </p>
                        </div>
                        <div className='personal__card'>
                            <img src={CardEvent} alt="event" />
                            <p className='personal__card-text'>   </p>
                            <p className='personal__card-textsmall'>15 , . </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
